* {
    box-sizing: border-box;
}

body {
    margin: 0;
}

.site-title {
    /* font-style: italic; */
    font-family:Cambria;
}

.site-title-container {
    display: flex;
    flex-direction: row;
}

.site-title-initial {
    font-size: 2rem;
    color: ivory;
}

.site-title-back {
    color: #A2B4BF;
    font-size: 2rem;
    font-weight: bold;
}

.nav-container{
    background-color: #24354B;
    z-index: 999;
    width: 100%;
    padding: 0 5rem;
}

.container {
    margin: 1rem;
    text-align: center;
}

.nav {
    background-color: #24354B;
    color: ivory;
    display: flex;
    justify-content: space-between;    
    align-items: center;
    gap: 2rem;
}

.nav ul{
    padding: 0;
    margin: 0;
    list-style: none;
    display: flex;
    gap: 3rem;
}

.nav li {
    border-radius: 0.5rem;
    padding: 0.25rem;
}

.nav li.active {
    background-color: #293241;
}

.nav li:hover {
    background-color: #293241;
}

.nav a {
    font-weight: bold;
    color: inherit;
    text-decoration: none;
    height: 100%;
    display: block;
    align-items: center;
    padding: .25rem;
}

/* .nav i {
    color: #BBDBEA;
} */

.toggle-button {
    position: absolute;
    top: 0.75rem;
    right: 1rem;
    display: none;
    flex-direction: column;
    justify-content: space-between;
    width: 30px;
    height: 22px;
    cursor: pointer;
}

.bar {
    height: 3px;
    width: 100%;
    background-color: #D1DBE7;
    border-radius: 10px;
}

.expanded {
    padding: 0 1.5rem;
    padding-right: 5rem;
}

@media (max-width: 800px) {
    .nav-container{
        padding: 0 1rem;
    }

    .toggle-button{
        display: flex;
    }

    .nav .expanded {
        /* display:  none; */
        width: 100%;
    }

    .nav {
        flex-direction: column;
        align-items: flex-start;
        color: #24354B;
    }

    .nav ul{
        width: 100%;
        flex-direction: column;
        gap: 0.15rem;
    }

    .nav li {
        text-align: center;
        padding: 0rem
    }

    .nav.active {
        display: flex;
    }

    .nav.nav.active{
        display: flex;
    }

    .expanded {
    position: absolute;
    background-color: #D1DBE7F5;
    z-index: 508;
    top: -100%;
    left: 0;
    width: 100%;
    /* height: 50vh; */
    overflow: hidden;
    padding: 3em;
    transition: all 2s cubic-bezier(0.16, 1, 0.1, 1);
    }
}